import { Component, Host, h, Prop } from '@stencil/core';
import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import type { BsImage } from '../../atoms/image/image';
import { TitleColor, TitleSize, TitleTag } from '../../atoms/title/title.types';

/**
 * Product card component.
 */
@Component({
  tag: 'bs-product-card',
  styleUrl: 'product-card.scss',
  shadow: true,
})
export class BsProductCard {
  /** Props */
  /**
   * The product card title.
   */
  @Prop() cardTitle: string;

  /**
   * The cta label.
   */
  @Prop() cardText: string;

  /**
   * The product card image.
   */
  @Prop() cardImage: BsImage | string;

  @Memoize('cardImage')
  get parsedCardImage(): BsImage {
    return parseIfString(this.cardImage);
  }

  render() {
    return (
      <Host>
        <div class="card">
          <div class="image">
            <bs-image {...this.parsedCardImage} caption="" />
          </div>
          <div class="content">
            <bs-title
              class="title"
              tag={TitleTag.Span}
              size={TitleSize.Head4}
              text={this.cardTitle}
              color={TitleColor.Black}
            />
            <bs-text class="text" text={this.cardText} />
          </div>
        </div>
      </Host>
    );
  }
}
